@use "global" as *;
.animation {
  overflow: hidden;
  margin-top: rem(15);
  @include mq(md) {
    margin-top: rem(130);
  }
}
.animation__text{
  @include mq(md) {
    font-size: rem(150);
  }
  font-family: $jp-font-family;
  font-weight: bold;
  font-size: rem(84);
  display : inline-block;
  color: #011950;
  white-space : nowrap;
  animation : text_scroll 15s linear infinite;
  padding-left:100%;
}
@keyframes text_scroll{
   0% { transform: translateX(0)}
   100% { transform: translateX(-100%)}
}


