@use "global" as *;
.btn {
  display: inline-block;
  position: relative;
  padding-right: rem(10);
  &::before {
    position: absolute;
  bottom: -3px;
  left: 0;
  content: '';
  width: 100%;
  height: 1px;
  background: #001E63;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: transform .3s;
  }
  &:hover::before {
    transform: scale(1, 1);
  }
  &--white::before {
    background: $white;

  }
  &::after {
    position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  content: "";
  vertical-align: middle;
  width: 0.5em;   /* arrow size */
  height: 0.5em;  /* arrow size */
  border-top: 1.5px solid #001E63;  /* thickness, color */
  border-right: 1.5px solid #001E63;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
    
  }
  &--white::after {
    border-top: 1.5px solid $white;  
    border-right: 1.5px solid $white;
  }
}
.section__btn--active .btn::after {
  border-top: 1.5px solid #001E63;  /* thickness, color */
  border-right: 1.5px solid #001E63;

}