@use "global" as *;
.works {
  background-color: #F8F8F8;
  padding-top: 60px;
  margin-top: rem(80);
  overflow: hidden;

  @include mq(md) {
    margin-top: 0;
  }
  &__btn {
    text-align: right;
  }
  &__inner {
    position: relative;
    @include mq(md) {
      max-width: rem(1100);
      margin: 0 auto;
      width: 90%;
    }
    
  }
}