@use "global" as *;
.sub-fv {
  position: relative;
  // margin-bottom: 200px;
  margin-top: rem(110);

  @include mq(md){
    height: 420px;
    margin-top: rem(240);
  }
  &__inner {
    margin-left: rem(10);
    height: inherit;
    @include mq(md){
      margin-left: 4.16%;
    }
  }
  &__top {
    position: absolute;
    background-color: $white;
    top: -80px;
    width: 52%;
    padding: rem(10) 0 rem(40) rem(8);
    @include mq(md){
      width: 34.7223%;
      padding: 0 0 rem(44) rem(50);
      top: -100px;
    }
  }
  &__title {
    font-weight: bold;
    font-family: $en-font-family-2;
    font-size: rem(44);
    font-weight: bold;
    @include mq(md){
      font-size: rem(100);

    }
  }
  &__ja-title {
    font-size: rem(12);
    font-weight: bold;
    @include mq(md){
      font-size: rem(20);
    }
  }
  &__img {
    height: 200px;
    @include mq(md){
      height: inherit;
      padding-left: 7.971%;
    }
    img {
      height: inherit;
    }
  }
}