@use "global" as *;
.footer {
  padding-top: rem(50);
  padding-bottom: rem(30);
  background-color: #001135;
  @include mq(md) {
    padding-top: rem(40);
    padding-bottom: rem(40);
  }
  &__contents {
    max-width: 275px;
    margin: 0 auto;
    @include mq(md) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      max-width: 1200px;
      padding: 0 10%;
    }
  }
  &__nav {
    @include mq(md) {
      order: 1;
      padding-right: rem(20);
    }
  }
  
  &__item {
    // padding: rem(12) 0;
    border-top: 1px solid #011950;
    height: 43px;
    display: flex;
    align-items: center;
    &:last-of-type {
      border-bottom: 1px solid #011950;
      @include mq(md) {
        border: none;
      }
    }
    @include mq(md) {
      border: none;

    }
  }
  &__link {
    color: $white;
    font-size: rem(14);
    height: inherit;
    width: 100%;
    align-items: center;
    display: flex;
    @include mq(md) {
      font-size: rem(16);
    }
  }
  &__icon {
    @include mq(md) {
      display: flex;
      align-items: center;
    }
  }
  &__logo {
    width: 108px;
    height: 80px;
    margin-top: rem(50);
    @include mq(md) {
      width: 243px;
      height: 180px;
    }
  }
  &__sns {
    margin-top: rem(50);
    @include mq(md) {
      margin-left: rem(60);
    }
  }
}
small {
  display: block;
  color: $white;
  font-size: rem(12);
  margin-top: rem(55);
  @include mq(md) {
    margin-top: rem(25);
  }
}
.footer__sns-icon {
  margin-left: 20px;
  &:first-of-type {
    margin-left: 0;
  }
}