@use "global" as *;
.about-copy {
  &--position {
    margin-top: rem(33);
    padding: 0 rem(20);
    @include mq(md){
      max-width: rem(840);
      margin: rem(65) auto 0;
      width: 90%;
      padding: 0;
    }
  }
  &__scroll {
    text-align: center;
  }
  &__link {
    display: inline-block;
    color: #011950;
    font-size: rem(12);
    position: relative;
    @include mq(md){
      font-size: rem(16);
    }
   &:not(:first-of-type) {
    margin-left: rem(55);
    @include mq(md){
      margin-left: rem(140);
    }
   }
   &::after {
    content: "";
    position: absolute;
    width: 8px;
    height: 8px;
    border-bottom: 2px solid #011950;
    border-right: 2px solid #011950;
    transform: rotate(45deg);
    right: -15px;
    top: 16%;
    border-radius: 1px;
    @include mq(md){
      right: -19px;
      top: 30%;
    }
   }
  }
  &__copy {
    font-size: rem(18);
    line-height: 2;
    margin-top: rem(62);
    @include mq(md){
      font-size: rem(40);
      line-height: 1.5;
      margin-top: rem(100);
    }
  }
  &__text {
    margin-top: rem(38);
    font-size: rem(14);
    line-height: 2.3;
    @include mq(md){
      font-size: rem(16);
      margin-top: rem(58);
      line-height: 2;
    }
  }
}