.btn {
  display: inline-block;
  position: relative;
  padding-right: 0.625rem;
}
.btn::before {
  position: absolute;
  bottom: -3px;
  left: 0;
  content: "";
  width: 100%;
  height: 1px;
  background: #001E63;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: transform 0.3s;
}
.btn:hover::before {
  transform: scale(1, 1);
}
.btn--white::before {
  background: #fff;
}
.btn::after {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  content: "";
  vertical-align: middle;
  width: 0.5em; /* arrow size */
  height: 0.5em; /* arrow size */
  border-top: 1.5px solid #001E63; /* thickness, color */
  border-right: 1.5px solid #001E63;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.btn--white::after {
  border-top: 1.5px solid #fff;
  border-right: 1.5px solid #fff;
}

.section__btn--active .btn::after {
  border-top: 1.5px solid #001E63; /* thickness, color */
  border-right: 1.5px solid #001E63;
}

