@use "global" as *;
.gnav {
  margin-left: auto;
  height: inherit;
  font-size: rem(16);
  font-weight: bold;
  &__list {
    display: flex;
    align-items: center;
    height: inherit;
  }
  &__item {
    height: inherit;
    position: relative;
    &::after {
      content: "";
      height: 6px;
      width: 6px;
      background-color: #011950;
      position: absolute;
      top: 56px;
      left: 50%;
      transform: translateX(-50%);
      opacity: 0;
      transition: .3s;
    }
    &:hover {
      &::after {
        opacity: 1;
        transition: .3s;
      }
    }
  }
  a {
    height: inherit;
    display: flex;
    align-items: center;
    padding: 0 25px;
  }
  &__item_contact {
    margin-left: rem(25);
    background-color: #011950;
    color: $white;
  }
  &__fa {
    margin-right: rem(10);
  }

}