@use "global" as *;
.works-posts {
  background-color: $white;
  position: relative;
  &::after {
    content: "";
    height: 100%;
    width: 200%;
    background-color: $white;
    position: absolute;
    top: 0;
    right: -1500px;
    z-index: 0;
  }
  &__inner {
    padding: 0 rem(30);
    margin-top: rem(17);
    padding-bottom: rem(80);

    @include mq(md){
      padding: rem(70) rem(50) 0 ;
      max-width: 1150px;
      margin-top: rem(31);
      padding-bottom: rem(120);
      z-index: 1;
      position: relative;

    }
  }
  &__contents {
    display: flex;
    padding: rem(30) 0;
    border-bottom: 1px solid #E2E2E2;
    @include mq(md){
      padding: rem(50) 0;
    }
  }
  &__img {
    width: 40.677966%;
    @include mq(md) {
      width: rem(290);
      height: rem(230);
      aspect-ratio: 290/230;
      width: 27.619048%;
    }
    img {
      object-fit: cover;
      aspect-ratio: 4/3;
      width: 100%;
      height: inherit;
    }
  }
  &__info {
    margin-left: rem(18);
    width: 53.220339%;
    @include mq(md) {
      margin-left: rem(50);
      width: 72.380952%;
    }
    span {
      display: inline-block;
      font-size: rem(10);
      color: #626262;
      text-align: center;
      padding: 7px 15px;
      background-color: #F3F3F3;
      @include mq(md){
        padding: 5px 20px;
      }
    }
  }
  &__title {
    font-size: rem(14);
    margin-top: rem(6);
    line-height: 2;
    position: relative;
    display: inline-block;

    @include mq(md){
      font-size: rem(24);
      &::after {
        content: '';
      height: 1px;
      background: $black;
      position: absolute;
      width: 100%;
      left: 0;
      bottom: -3px;
      transform: scale(0, 1);
      transform-origin: left top;
      transition: transform .3s;
      }
    }
  }
  &__post:hover{
    .works-posts__title::after {
      transform: scale(1, 1);

    }
  }
  &__excerpt {
    margin-top: rem(30);
    font-size: rem(16);
    color: #626262;
    line-height: 2.375;
  }
  &__date {
    font-size: rem(10);
    color: #A9A9A9;
    margin-top: rem(20);
    display: inline-block;
    @include mq(md){
      margin-top: rem(22);
      font-size: rem(12);
    }
  }
  
}