@use "global" as *;
.fv {
  // height: 100vh;
  padding: 0 rem(10);
  // position: absolute;
  // top: rem(10);
  width: 100%;
  margin-top: -72px;
  @include mq(md) {
    // height: calc(100vh - 80px);
    padding: 0 rem(80) 0 rem(40);
    position: relative;
    top: 0;
    margin-top: 0;
  }
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 74.666%;
    height: 199%;
    background-color: #F8F8F8;
    z-index: -1;
    @include mq(md) {
      width: 56.9444%;
      height: 195.7%;
    }
  }
  &__inner {
    max-width: 1320px;
    width: 100%;
    margin: 0 auto;

  }
  &__flex {

    @include mq(md) {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  &__slick {
    
    @include mq(md) {
      width: 83.333333%;
      flex-shrink: 0;
    }
  }
  &__copy {
    font-size: rem(34);
    font-weight: bold;
    padding: rem(30) 0 0 rem(10);
    @include mq(md) {
      width: 11.363636%;
      writing-mode: vertical-rl;
      font-size: rem(60);
      margin-left: rem(70);
    }
  }
  &__en {
    @include mq(md) {
      display: flex;
      justify-content: flex-end;
      margin-top: -14%;
    }
  }
  &__en-copy {
    font-size: rem(23);
    margin: rem(16) 0 0 rem(10);
    padding-bottom: rem(10);
    border-bottom: 2px solid #000;
    @include mq(md) {
      font-size: rem(40);
      padding: 0 rem(190) rem(20) 0;
      border-bottom: 3px solid #000;
      z-index: 10;
    }
  }
}
.slick-arrow:before{
	content:""!important;
  display: none !important;
}

.fv-service {
  display: none;
  max-width: 930px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 100px;
  width: 90%;
  z-index: 999;
  background-color: $white;
  &__inner {
    padding: rem(50) rem(60);
    display: flex;
    justify-content: space-between;
  }
  &__title {
    font-weight: bold;
    font-family: $en-font-family-2;
    font-size: rem(30);
  }
  &__contents {
    display: flex;
  }
  &__link {
    display: block;
    margin-left: 3.5%;
    width: calc((100% - (3.5% * 2)) / 3);
    img {
      aspect-ratio: 9/7;
      width: 180px;
      height: 140px;
    }
  }
  &__link:nth-child(3n + 1) {
    margin-left: 0;
  }
  &__name {
    font-size: rem(14);
    margin-top: rem(14);
    position: relative;
    display: inline-block;
    &::after {
      content: '';
      height: 1px;
      background: $black;
      position: absolute;
      width: 100%;
      left: 0;
      bottom: -3px;
      transform: scale(0, 1);
      transform-origin: left top;
      transition: transform .3s;
    }
    
  }
  
  
}
.fv-service__link:hover {
  .fv-service__name::after {
    transform: scale(1, 1);

  }
}
.body-bg {
  position: fixed;
  width: 100%;
  height: 100%;
  background: grey;
  opacity: .6;
  z-index: 15;
  top: 0;
  left: 0;
  right: 0;
}
.modal-show{
  display: block;
}
