@use "global" as *;
.about-company {
  background-color: #F8F8F8;
  &--position {
    margin-top: 80px;
    @include mq(md){
      margin-top: 120px;
    }
  }
  &__wrapper {
    padding: 63px 20px 80px 20px;
    @include mq(md){
      max-width: 1100px;
      margin: 0 auto;
      width: 90%;
      padding: 75px 0 120px ;
    }
  }
  &__contents {
    margin-top: 10px;
    @include mq(md){
      margin-top: 20px;
    }
  }
  &__content {
    padding: 24px 0 20px;
    font-size: rem(14);
    border-bottom: 1px solid #E2E2E2;
    @include mq(md){
      font-size: rem(16);
      display: flex;
      padding: 45px 0 43px;
    }
  }
  &__dis {
    @include mq(md){
      width: 15.4545%;
    }
  }
  &__txt {
    margin-top: 22px;
    @include mq(md){
      width: 84.5454%;
      margin-top: 0;
    }
    &--ml {
      margin-left: 29px;
      @include mq(md){
        margin-left: 0;
      }
    }
    &--width {
      @include mq(md){
        width: 11%;
      }
    }
    
  }
  &__flex {
    display: flex;
    @include mq(md){
      width: 84.5454%;
    }
  }
  &__info {
    @include mq(md){
      width: 84.5454%;
    }
    .about-company__txt {
      &:not(:first-of-type) {
        @include mq(md){
  
          margin-top: 26px;
        }
        
        
      }
    }
    
  }
  &__slick {
    margin-bottom: 80px !important;

    @include mq(md){

      margin-bottom: 120px !important;
    }
  }
  &__img {
    margin-left:5px !important;
    @include mq(md){
      margin-left:10px !important;
    }

  }
}