@use "global" as *;
.service-section {
  @include mq(md){
    max-width: 1440px;
    margin-left: auto;
    margin-right: auto;
  }
  &--position {
    margin-top: 50px;
    @include mq(md){
      margin-top: 120px;
    }
  }
  // &__wrapper {
  //   padding-left: 20px;
  // }
  &__flex {
    background-color: #F8F8F8;
    @include mq(md){
      display: flex;
      padding-top: 4.86%;
    padding-left: 11.8%;
    }

  }
  &__head {
    padding-left: 20px;
    padding-top: 60px;
    @include mq(md){
      width: 31.49%;
      padding-top: 0;
      padding-left: 0;
    }
    &-txt {
      font-size: 14px;
      margin-top: 24px;
      line-height: 2.28;
      @include mq(md){
        font-size: 14px;
      }
      @include mq(lg){
        font-size: 18px;
      }
      @include mq(xl){
        font-size: 22px;
        margin-top: 25px;
      }

    }
  }
  &__num {
    font-size: 32px;
    font-weight: bold;
    font-family: $en-font-family-2;
    color: #001E63;
    position: relative;
    display: inline-block;
    @include mq(md){
      font-size: 40px;
    }
    &::after {
      content: "";
      position: absolute;
      height: 30px;
      background-color: $black;
      width: 1.5px;
      top: 50%;
      transform: translateY(-50%);
      right: -10px;
    }

  }
  &__title {
    font-size: 20px;
    font-weight: bold;
    margin-top: 25px;
    @include mq(md){
      font-size: 28px;
      margin-top: 27px;
    }
  }
  &__img {
    width: 86.666%;
    margin-left: auto;
    margin-top: 36px;
    @include mq(md){
      width: 59.84%;
      margin-top: 0;
    }
  }
  &__bottom {
    width: 86.666%;
    margin-right: auto;
    padding: 30px 0 36px 20px;
    z-index: 10;
    position: relative;
    margin-top: -50px;
    background-color: $white;
    @include mq(md){
      padding-left: 11.8%;
      padding-top: 40px;
      padding-bottom: 48px;
      padding-right: 40px;
      max-width: 860px;
    }
    @include mq(xl){
      padding-left: 11.8%;
    padding-top: 40px;
    padding-bottom: 48px;
    padding-right: 40px;
    margin-top: -13%;
    max-width: 860px;
    }
    &-txt {
      font-size: 14px;
      margin-top: 20px;
      line-height: 2.28;
      @include mq(md){
        font-size: 16px;
    line-height: 2.375;
      }
    }
  }
  &__features {
    font-size: 18px;
    font-family: $en-font-family-2;
    position: relative;
    display: inline-block;
    @include mq(md){
      font-size: 22px;
    }
    &::after {
      bottom: 3px;
    color: #626262;
    content: "特徴";
    font-size: 10px;
    position: absolute;
    right: -30px;
    }
  }

  
}
#section3 {
  @include mq(md){
    margin-bottom: 120px;
  }
}