@use "global" as *;
.media {
  &__contents {
    @include mq(md) {
      display: flex;
      align-items: center;
    }

  }
  &__img {
    @include mq(md) {
      width: 50%;
    }
  }
  &__texts {
    margin-top: rem(30);
    @include mq(md) {
      width: 50%;
      padding-left: 3.75em;
      margin-top: 0;
    }
  }
  &__text {
    font-size: rem(14);
    margin-top: rem(30);
    line-height: 2.2857;
    @include mq(md) {
      font-size: rem(16);
      margin-top: rem(40);
      line-height: 2.375;
    }
  }
}