@use "global" as *;
.about-title {
  position: relative;

  &__japanese {
    color: #001E63;
    font-size: rem(12);
    @include mq(md){
      font-size: rem(16);
    }
  }
  &__english {
    font-size: rem(26);
    font-weight: bold;
    font-family: $en-font-family-2;
    position: relative;
    display: inline-block;
    @include mq(md){
      font-size: rem(40);
    }
    &::after {
      content: "";
      position: absolute;
      width: 60px;
      height: 2px;
      background-color: $black;
      top: 50%;
      transform: translateY(-50%);
      // left: 124px;
      right: -70px;
      @include mq(md){
        // left: 28.45%;
        right: -80px;
      }
    }
  }
}