@use "global" as *;

.section {
  &__btn {
    display: inline-block;
    text-align: right;
    margin-top: rem(40);
    font-size: rem(14);
    color: #001E63;
    font-family: $en-font-family-1;
    &--box {
      padding: rem(12) rem(34) rem(12) rem(24);
      border: 1px solid #001E63;
      border-radius: 5px;
    }
    &--white {
      color: $white;
      border: 1px solid $white;

    }
    &--active {
      color: #001E63;
      border: 1px solid #001E63;

    }
  }
  &__inner {
    margin-left: rem(20);
    
  }
  &__mt {
    margin-top: rem(80);
    @include mq (md) {
      margin-top: rem(120);
    }
  }
  &__title {
    font-size: rem(38);
    font-weight: bold;
    font-family: $en-font-family-2;
    @include mq(md) {
      font-size: rem(70);

    }
    &--right {
      text-align: right;
      padding-right: 13.8888%;
    }
  }
}