@use "global" as *;
.news-posts {
  margin-top: 85px;
  position: absolute;
  width: 100%;
  @include mq(md){
    margin-top: 0;
    padding-left: 5.4545%;
    border-left: 1px solid #E2E2E2;
    position: relative;
  }
  &__post {
    padding: 20px 0 25px 0;
    border-top: 1px solid #E2E2E2;
    &:last-of-type {
      border-bottom: 1px solid #E2E2E2;
    }
    &:first-of-type {

      @include mq(md){
        border-top: none;
      }
    }
    @include mq(md){

      &:hover{
        .news-posts__title::after {
          transform: scale(1, 1);
    
        }
      }
    }
  }
  &__info {
    display: flex;
    align-items: center;
    // flex-wrap: wrap;
  }
  &__date {
    font-size: rem(10);
    color: #A9A9A9;
    font-weight: bold;
    width: 15.38%;
    margin-right: rem(10);
  }
  &__category {
    font-size: rem(10);
    color: #626262;
    background-color: $white;
    padding: rem(7) rem(15);
    margin-left: rem(6);
    &--page {
      color: #626262;
      background-color: #F8F8F8;
    }
  }
  &__wrap {
    width: 84.61%;
    display: flex;
    flex-wrap: wrap;
    row-gap: 6px;
  }
  &__title {
    margin-top: rem(20);
    font-size: rem(14);
    position: relative;
    display: inline-block;
    @include mq(md){
      &::after {
        content: '';
        height: 1px;
        background: $black;
        position: absolute;
        width: 100%;
        left: 0;
        bottom: -3px;
        transform: scale(0, 1);
        transform-origin: left top;
        transition: transform .3s;
      }
    }
  }
}