@use "global" as *;
.service {
  // margin-top: rem(80);
  &__inner {
    margin-top: rem(80);
   padding-bottom: rem(80);
  }
  &__cards {
    margin-top: rem(20);
  }
  &__card {
    &:not(:first-of-type) {
      margin-top: rem(60);
    }
  }
  &__img {
    position: relative;
  }
  &__img-text {
    position: absolute;
    top: 55px;
    left: 30px;
    color: $white;
  }
  &__no {
    font-size: rem(16);
    font-family: $en-font-family-2;

  }
  &__title {
    font-size: rem(26);
    margin-top: rem(17);

    &--mt-bg {
      margin-top: rem(27);
    }
  }
  &__body {
    margin-top: rem(20);
  }
  &__text {
    font-size: rem(14);
    line-height: 2.285;
  }
  &__btn {
    margin-right: rem(26);
    text-align: right;
  }
}

// pc
.service {
  &__wrapper {
    margin-top: 120px;
  }
  
  &__wide-img {
    width: 100%;
    height: inherit;
    position: absolute;
    z-index: -1;
  }
  &__bgi {
   background-image: url(../images/service_0.jpg);
   background-position: top center;
   background-size: cover;
  }
  &__items {
    display: flex;
    align-items: center;
    height: rem(460);
    margin-top: rem(40);
  }
  &__item {
    width: 33.333%;
    text-align: center;
    height: inherit;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 5em;
    color: $white;
    &:not(:first-of-type) {
      border-left: 1px solid #E2E2E2;
    }
    &--black {
      color: #000;
    }

  }
  &__copy {
    color: $white;
    font-size: rem(16);
    line-height: 2.375;
    margin-top: rem(35);
    display: none;
    &--active {
      display: block;
    }
  }
}