.breadcrumbs {
  margin-top: rem(10);
  font-size: rem(10);
  position: relative;
  padding: 0 rem(10);
  text-align: right;
  @include mq(md){
    margin-top: rem(20);
   padding: 0 60px;
   max-width: 1440px;
   margin: rem(20) auto 0;
  }
  span {
    color: #A9A9A9;
    // display: inline-block;
  }
  span:last-of-type {
    color: $black;
  }
  span:not(:first-of-type) {
    margin-left: 40px;
    position: relative;
  }
  span:not(:first-of-type)::before {
    content: "";
    height: 1px;
    width: 20px;
    background-color: #A9A9A9;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: -30px;
  }
  // span:nth-of-type(3) {
  //   width: 90px;
  //   display: inline-block;
  //   // display: none;
  // }
}

.breadcrumbs span[property="name"] {
    display: inline-block;
    padding: 0;
    margin-top: -3px;
    vertical-align: middle;
    max-width: 110px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
