@use "global" as *;
.top-contact {
  background-color: #011950;
  color: $white;
  &__flex {
    @include mq(md){
      display: flex;
      align-items: center;
    }
  }
  &__inner {
    max-width: rem(225);
    margin: 0 auto;
    width: 90%;
    padding: rem(84) 0 rem(80);
    @include mq(md){
      max-width: rem(270);
      // padding-bottom: rem(83);
      padding: 0;
      width: 43%;
    } 
  }
  &__img {
    @include mq(md){
      order: -1;
      width: 57%;
    }
    img {
      @include mq(md){
        height: 445px;
      }
    }
  }
  &__text {
    font-size: rem(14);
    line-height: 2;
    @include mq(md){
      font-size: rem(18);
    }
  }
  &__btn {
    border: 1px solid $white;
    border-radius: 5px;
    margin-top: rem(42);
    @include mq(md){
      margin-top: rem(35);
    }
    a {
      font-size: rem(16);
      display: block;
      padding: rem(23) 0;
      text-align: center;
      @include mq(md){
        font-size: rem(22);
        padding: rem(23) 0;
      }
    }
  }
  &__tel {
    border: 1px solid $white;
    border-radius: 5px;
    margin-top: rem(20);

    a {
      font-size: rem(16);
      display: block;
      padding: rem(23) 0;
      text-align: center;
      @include mq(md){
        font-size: rem(22);
        padding: rem(23) 0;
        pointer-events: none;
      }
    }
  }
  &__time {
    font-size: rem(12);
    margin-top: rem(13);
    @include mq(md){
      margin-top: rem(24);
      font-size: rem(14);
    }
   
  }
  
}
.fa-phone {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  margin-right: rem(10);
  @include mq(md){
    width: 22px;
    height: 22px;
  }
}