@use "global" as *;
.header {
  height: 80px;
  position: relative;
  max-width: 1440px;
  margin: 0 auto;
  z-index: 999;
  background-color: $white;
  &__inner {
    width: 100%;
    height: inherit;
    display: flex;
    align-items: center;
  }
  &__logo {
    position: absolute;
    left: 0;
    top: 0;
    width: rem(108);
    height: rem(80);
    @include mq(md) {
      width: rem(243);
      height: rem(180);
    }
  }
  &__mb {
    margin-bottom: 120px;
  }
}