@use "global" as *;
.about-philosophy {
  background-color: #F8F8F8;
  max-width: 1140px;
  position: relative;
  @include mq(md){
    width: 79.166%;
  }
  &--position {
    margin-top: rem(86);
    @include mq(md){
      margin-right: auto;
    }
  }
  &__wrapper {
    padding: rem(63) rem(20) rem(250);
    @include mq(md){
      max-width: 850px;
      width: 74.56%;
      padding: rem(75) 0 rem(73) 14.9%;
    }
  }
  &__text {
    font-size: rem(14);
    margin-top: rem(30);
    line-height: 2.285;
    @include mq(md){
      margin-top: rem(60);
      line-height: 2.375;
    }
  }
  &__name {
    font-size: rem(14);
    margin-top: rem(40);
    text-align: right;
    @include mq(md){
      margin-top: rem(53);
    }
  }
  &__img {
    // display: none;
    position: absolute;
    aspect-ratio: 18/25;
    width: 220px;
    bottom: -90px;
    right: 20px;
    @include mq(md){
      // width: 360px;
      width: 31.57%;
      bottom: -80px;
      right: -11%;
    }
  }
}