@use "global" as *;
.about-access {
  @include mq(md){

    max-width: 1270px;
    width: 88.1944%;
  }
  &--position {
    margin-top: rem(175);
    margin-left: rem(20);
    @include mq(md){
      margin: rem(205) 0 0 auto;
    }
  }
  &__info {
    margin-top: 24px;
    @include mq(md){
      display: flex;
      margin-top: 30px;
    }
  }
  &__post {
    font-size: rem(14);
    
  }
  &__address {
    font-size: rem(14);
    margin-top: rem(9);
    @include mq(md){
      margin-top: 0;
      margin-left: 18px;
    }
  }
  iframe {
    width: 100%;
    margin-top: rem(21);
    @include mq(md){
      margin-top: 44px;
    }
  }
 
}