@use "global" as *;
.news {
  background-color: #F8F8F8;
  display: flex;
  // padding-bottom: 500px;
  min-height: 950px;
  @include mq(md){
    
    min-height: 885px;
  }
  &__padding {
    padding: rem(60) rem(30) 0 rem(20);
    @include mq(md){
      padding-right: 0;
    padding-left: 0;
    }

  }
  &__page-padding {
    padding-left: 8.8%;
    padding-right: 8.8%;
    @include mq(md){

    }
  }
  &__page-bg {
    background-color: $white;
  }
  &__inner {
    @include mq(md){
      max-width: rem(1100);
      margin: 0 auto;
      width: 90%;
      display: flex;
    }
  }
  &__left {
      width: 24%;
      height: 320px;
      position: sticky;
      top: 0;
  }
  &__contents {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    margin-top: rem(40);
    row-gap: 20px;
    @include mq(md){
      width: 76%;
    }
  }
  &__left-tab {
    margin-top: rem(50);
  }
  &__tab {
    font-size: rem(12);
    color: #A9A9A9;
    cursor: pointer;
    position: relative;
    padding-left: 14px;
    @include mq(md){
      font-size: rem(14);
      margin-top: rem(26);
    }
    &:first-of-type {
      @include mq(md){
        margin-top: 0;
      }
    }
    
    
  }
  &__category {
  //  position: absolute;
  //  width: 100%;
  width: 50%;
  }
  &__btn {
    text-align: right;

  }
  &__btn-left {
    margin-top: rem(25);
    color: #001E63;
    padding-left: rem(14);
  }
  
}
.active, .hover {
  // background:#999;
  color: #000;
  font-weight:bold;
}
.news__tab.tabchecked::before {
  content: "";
     width: 8px;
     height: 8px;
     background-color: #001E63;
     margin-right: 6px;
     position: absolute;
    left: 0px;
    transition: .5s;
    top: 50%;
    transform: translateY(-50%);
}
.tabchecked {
  color: #000;
  transition: .5s;
}
